import React, {Component} from 'react'
import {Link} from 'react-router-dom'
import { Swipeable } from 'react-swipeable'
class View extends Component{
    constructor(props) {
        super(props);
        this.state = {
            page:1
        }
    }
    componentDidMount(){
        this.props.getObjectView(this.props.match.params.id)
    }
    goToPage(page){
        this.setState({page:page})
    }
    render(){
        return(<>
            <div className="page-view">
                <Swipeable onSwipedLeft={()=>{this.goToPage(this.state.page+1)}} onSwipedRight={()=>{this.goToPage(this.state.page-1)}} onSwipedDown={()=>{}} onSwipedUp={()=>{}}>
                    <img src={"http://elib.nlu.org.ua/"+this.props.object.path+"/jpg/"+(this.props.object.pages ? this.props.object.pages[this.state.page] : "")}/>
                </Swipeable>
            </div>
         </>
        )
    }
}
export default View
